<template>
  <div class="the-navbar__user-meta flex items-center">

    <div class="text-right leading-tight hidden sm:block">
      <p class="font-semibold">{{ currentUser.first_name }}{{ currentUser.last_name }}</p>
      <small v-if="currentUser.is_superuser && currentUser.is_staff">Administrateur</small>
      <small v-else>Commercial</small>
    </div>

    <vs-dropdown vs-custom-content vs-trigger-click class="cursor-pointer">

      <div class="con-img ml-3">
        <img v-if="activeUserInfo.photoURL" key="onlineImg" :src="activeUserInfo.photoURL" alt="user-img" width="40" height="40" class="rounded-full shadow-md cursor-pointer block" />
      </div>

      <vs-dropdown-menu class="vx-navbar-dropdown">
        <ul style="min-width: 9rem">

          <li @click="$router.push('profil')" class="flex py-2 px-4 cursor-pointer hover:bg-primary hover:text-white">
            <feather-icon icon="UserIcon" svgClasses="w-4 h-4" />
            <span class="ml-2">Profil</span>
          </li>

          <vs-divider class="m-1" />

          <li
            class="flex py-2 px-4 cursor-pointer hover:bg-primary hover:text-white"
            @click="Logout">
            <feather-icon icon="LogOutIcon" svgClasses="w-4 h-4" />
            <span class="ml-2">Déconnexion</span>
          </li>
        </ul>
      </vs-dropdown-menu>
    </vs-dropdown>
  </div>
</template>

<script>

import axios from 'axios'

export default {
  data() {
    return {

      currentUser: []

    }
  },
  computed: {
    activeUserInfo() {
      return this.$store.state.AppActiveUser
    }
  },
  beforeMount() {
    this.GetCurrentUser()
  },
  methods: {
    async GetCurrentUser() {
      await axios
        .get("/authentification/user/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.currentUser = res.data;
        })
        .catch(() => {
          localStorage.clear(), this.$router.push("/connexion");
        });
    },
    async Logout() {
      await axios
        .post("/authentification/logout/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          localStorage.clear(), this.$router.push("/connexion");
        });
    },
  }
}
</script>
